@keyframes sonarWave {
  from {
    opacity: 0.4;
  }
  to {
    transform: scale(2);
    opacity: 0;
  }
}
.sonar-emitter {
  position: relative;
  z-index: 1;
}
.sonar-emitter::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  content: "";
  background-color: var(--bs-primary-500);
  z-index: -1;
  pointer-events: none;
  opacity: 0;
  animation: sonarWave 2s linear infinite;
}

